import { createContext, useCallback, useMemo } from 'react';
import { LOCAL_STORAGE_KEYS } from '@constants/config';
import { darkTheme, lightTheme, Themes } from '@constants/themes';
import { useStickyState } from '@hooks/useStickyState';
import { ThemeProvider } from 'styled-components';

import { Theme } from '@/types/theme';

import { ThemeContextProps, ThemeContextProviderProps } from './types';

export const ThemeContext = createContext<ThemeContextProps>({
  theme: darkTheme,
  toggleTheme: () => {},
});

export const ThemeContextProvider = ({
  children,
}: ThemeContextProviderProps) => {
  const [theme, setTheme] = useStickyState<Theme>(
    darkTheme,
    LOCAL_STORAGE_KEYS.APP_THEME,
  );

  const toggleTheme = useCallback(() => {
    const newTheme = theme.mode === Themes.DARK ? lightTheme : darkTheme;
    setTheme(newTheme);
  }, [theme, setTheme]);

  const themeContext = useMemo(
    () => ({ theme, toggleTheme }),
    [theme, toggleTheme],
  );

  return (
    <ThemeContext.Provider value={themeContext}>
      <ThemeProvider theme={theme}>{children}</ThemeProvider>
    </ThemeContext.Provider>
  );
};
