import { API_CONFIG, FETCH_BANKS_CONFIG } from '@constants/config';

import { FetchBanksProps } from '@/types/banks';
import { mapPlacesToBank } from '@/utils/mapPlacesToBank';

export const fetchBanks = async (props: FetchBanksProps) => {
  const {
    location: { lat, lng },
    radius,
  } = props;

  const { FOURSQUARE_API_KEY, FOURSQUARE_API_URL } = API_CONFIG;
  const { categoriesId, banksLimit } = FETCH_BANKS_CONFIG;

  const url = `${FOURSQUARE_API_URL}?ll=${lat},${lng}&radius=${radius}&categories=${categoriesId}&limit=${banksLimit}`;

  try {
    const response = await fetch(url, {
      headers: {
        Authorization: FOURSQUARE_API_KEY!,
      },
    });

    const data = await response.json();

    return mapPlacesToBank(data.results);
  } catch (error) {
    console.error('Error fetching banks: ', error);
    return [];
  }
};
