import { Suspense } from 'react';
import { MainLoader } from '@components/Loaders/MainLoader';
import { ROUTES } from '@constants/routes';
import { useCachableStoreRequest } from '@hooks/useCachableStoreRequest';
import { Layout } from '@layout/Layout';
import { selectCurrenciesState } from '@store/selectors/currencySelector';
import { fetchCurrenciesThunk } from '@store/slices/currencySlice';
import { BrowserRouter, Route, Routes } from 'react-router-dom';

export const Router = () => {
  const { isLoading } = useCachableStoreRequest({
    cacheInterval: 60 * 1000,
    selector: selectCurrenciesState,
    thunk: fetchCurrenciesThunk,
  });

  return (
    <BrowserRouter>
      <Layout>
        <Suspense fallback={<MainLoader />}>
          {isLoading ? (
            <MainLoader />
          ) : (
            <Routes>
              {ROUTES.map(({ path, element }) => (
                <Route key={path} path={path} element={element} />
              ))}
            </Routes>
          )}
        </Suspense>
      </Layout>
    </BrowserRouter>
  );
};
