import { FETCH_BANKS_CONFIG } from '@constants/config';

import { Bank, FoursquarePlaceResult } from '@/types/banks';

import { getRandomCurrencies } from './getRandomCurrencies';

export const mapPlacesToBank = (places: FoursquarePlaceResult[]): Bank[] => {
  const {
    currenciesLimit: { min, max },
  } = FETCH_BANKS_CONFIG;

  return places.map(({ fsq_id, name, location, geocodes }) => ({
    id: fsq_id,
    name: name,
    address: location.formatted_address || 'Unknown address',
    location: {
      lat: geocodes.main.latitude,
      lng: geocodes.main.longitude,
    },
    currencySupport: getRandomCurrencies(min, max),
  }));
};
