import { API_CONFIG } from '@constants/config';
import axios from 'axios';

import { CurrencyHistoryData } from '@/types/financial';

export const fetchCurrencyHistory = async (
  currency: string,
  startDate: string,
  endDate: string,
) => {
  const { COIN_API_HISTORY_URL, COIN_API_KEY } = API_CONFIG;

  try {
    const response = await axios.get(
      `${COIN_API_HISTORY_URL}/${currency}/USD/history`,
      {
        headers: { 'X-CoinAPI-Key': COIN_API_KEY },
        params: {
          period_id: '1DAY',
          time_start: startDate,
          time_end: endDate,
        },
      },
    );

    console.log(response.data);

    return response.data.map((data: CurrencyHistoryData) => ({
      time_open: data.time_open,
      rate_close: data.rate_close,
    }));
  } catch (error) {
    console.error(error);
    throw new Error('Failed to fetch data history');
  }
};
