import { fetchBanks } from '@api/bank/fetchBanks';
import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';

import { Bank, BankState, FetchBanksProps } from '@/types/banks';

const initialState: BankState = {
  banks: [],
  selectedCurrency: null,
  isLoading: false,
  error: null,
};

export const fetchBanksThunk = createAsyncThunk(
  'banks/fetchBanks',
  async ({ location, radius }: FetchBanksProps) => {
    return await fetchBanks({ location, radius });
  },
);

const bankSlice = createSlice({
  name: 'banks',
  initialState,
  reducers: {
    setSelectedCurrency(state, action: PayloadAction<string | null>) {
      state.selectedCurrency = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchBanksThunk.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(
        fetchBanksThunk.fulfilled,
        (state, action: PayloadAction<Bank[]>) => {
          state.isLoading = false;
          state.banks = action.payload;
        },
      )
      .addCase(fetchBanksThunk.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.error.message || 'Failed to fetch banks';
      });
  },
});

export const { setSelectedCurrency } = bankSlice.actions;
export default bankSlice.reducer;
